<template>
  <!-- Projects grid -->
  <section class="pt-10 sm:pt-14">
    <!-- Projects grid title -->
    <div class="text-center">
      <p
        class="font-general-semibold text-2xl sm:text-5xl font-semibold mb-2 text-ternary-dark dark:text-ternary-light"
      >
        {{ projectsHeading }}
      </p>
    </div>

    <!-- Filter and search projects -->
    <div class="mt-10 sm:mt-10">
      <h3
        class="font-general-regular text-center text-secondary-dark dark:text-ternary-light text-md sm:text-xl font-normal mb-4"
      >
        Buscar proyectos por título o filtrar por categoría
      </h3>
      <div
        class="flex justify-between border-b border-primary-light dark:border-secondary-dark pb-3 gap-2"
      >
        <div class="flex justify-between gap-2">
          <span
            class="hidden sm:block bg-primary-light dark:bg-ternary-dark p-2.5 shadow-sm rounded-xl cursor-pointer"
          >
            <i
              data-feather="search"
              class="text-ternary-dark dark:text-ternary-light"
            ></i>
          </span>
          <input
            v-model="buscador"
            class="font-general-medium pl-3 pr-1 sm:px-4 py-2 border-1 border-gray-200 dark:border-secondary-dark rounded-lg text-sm sm:text-md bg-secondary-light dark:bg-ternary-dark text-primary-dark dark:text-ternary-light"
            id="name"
            name="name"
            type="search"
            required=""
            placeholder="Buscar proyectos"
            aria-label="Name"
          />
        </div>
        <ProjectsFilter @filter="categoria = $event" />
      </div>
    </div>

    <div class="grid-flow-col lg:grid-cols-1 mt-2" v-if="loading">
      <h1 class="flex justify-center text-indigo-500">
        Cargando portafolio...
      </h1>
    </div>
    <!-- Projects grid -->
    <div class="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 mt-6 sm:gap-10">
      <ProjectSingle
        v-for="project in verPortafolio"
        :key="project.id"
        :project="project"
      />

      <!-- <p v-for="item in verPortafolio" :key="item.id">
				{{item}}
			</p> -->
    </div>
  </section>
</template>

<script>
import { ref, computed } from "vue";
import feather from "feather-icons";
import ProjectsFilter from "./ProjectsFilter.vue";
import ProjectSingle from "./ProjectSingle.vue";
import usePortafolio from "../../composables/usePortafolio";

export default {
  components: { ProjectSingle, ProjectsFilter },
  data: () => {
    return {
      projectsHeading: "Proyectos Portafolio",
    };
  },
  mounted() {
    feather.replace();
  },
  setup() {
    const buscador = ref();
    const categoria = ref();

    const { portafolio, loading } = usePortafolio();

    const filtrarProjectos = () => {
      let project = new RegExp(buscador.value, "i");
      return portafolio.value.filter((el) =>
        el.attributes.titulo.match(project)
      );
    };

    const filtrarCategoria = () => {
      return portafolio.value.filter((item) => {
        let category =
          item.attributes.categoria.data.attributes.categoria
            .charAt(0)
            .toUpperCase() +
          item.attributes.categoria.data.attributes.categoria.slice(1);
        console.log(category);
        return category.includes(categoria.value);
      });
    };

    const verPortafolio = computed(() => {
      if (categoria.value) {
        return filtrarCategoria();
      } else if (buscador.value) {
        return filtrarProjectos();
      }
      return portafolio.value;
    });

    return {
      portafolio,
      verPortafolio,
      buscador,
      categoria,
      loading,
    };
  },
};
</script>

<style scoped></style>
