import { ref } from "vue";
import axios from "axios";
import { API_PRODUCCION } from "../utils/constants";

const usePortafolio = () => {
  const portafolio = ref();
  const loading = ref(false);

  const verPortafolio = async () => {
    try {
      portafolio.value = null;

      loading.value = true;

      const { data } = await axios.get(
        `${API_PRODUCCION}/api/portafolios?populate=img_big,img_small,categoria`
      );

      portafolio.value = data.data;
    } catch (error) {
      console.log(error);
    }
    loading.value = false;
  };

  verPortafolio();

  return {
    portafolio,
    loading,
  };
};

export default usePortafolio;
