<template>
  <div class="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-1 mt-20 sm:gap10">
    <a
      @click="showModal()"
      aria-label="Single Project"
    >
      <img
        src="https://res.cloudinary.com/rg-infinity/image/upload/v1649370336/tienda_1_8d5c53998e.png"
        class="rounded-xl cursor-img shadow-lg hover:shadow-xl cursor-pointer mb-10 sm:mb-0"
      />
    </a>
    <div class="grid-flow-col mt-3 sm:mt-3">
      <div class="flex justify-center">
        <a
          href="http://ecommerce.gonzalo-web.x10.mx/"
          class="font-general-medium flex items-center px-6 py-3 rounded-lg shadow-lg hover:shadow-xl bg-indigo-500 hover:bg-indigo-600 focus:ring-1 focus:ring-indigo-900 text-white text-lg sm:text-xl duration-300"
          target="__blank"
          >
          <i data-feather="eye" class="mr-1"></i>
           Demo</a
        >
      </div>
    </div>
  </div>

  <ModalTienda :showModal="showModal" :modal="modal" />
</template>

<script>
import ModalTienda from "./ModalTienda.vue";
import feather from "feather-icons";
export default {
  components: {
    ModalTienda,
  },
  data() {
    return {
      modal: false,
    };
  },
  methods: {
    showModal() {
      if (this.modal) {
        // Stop screen scrolling
        document
          .getElementsByTagName("html")[0]
          .classList.remove("overflow-y-hidden");
        this.modal = false;
      } else {
        document
          .getElementsByTagName("html")[0]
          .classList.add("overflow-y-hidden");
        this.modal = true;
      }
    },
  },
   mounted() {
    feather.replace();
  },
  updated() {
    feather.replace();
  },
};
</script>

